<template>
  <div class="priceadd">
    <v-dialog v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="enterAdd"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row class="align-center">
              <v-col cols="4">
                <v-text-field
                  label="日期"
                  type="date"
                  v-model="list.date"
                  :rules="emptyRules"
                  readonly
                ></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-text-field
                  label="數量"
                  type="number"
                  v-model="list.quantity"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="6" v-if="process == 'add'">
                <v-autocomplete
                  :items="feeItems"
                  label="計價項目選擇"
                  v-model="list.fee"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
              </v-col>
              <v-col cols="6" v-if="process == 'edit'">
                <v-text-field
                  label="計價項目選擇"
                  :value="list.name + '-' + list.kind"
                  readonly
                  outlined
                ></v-text-field>
              </v-col>
              <v-col cols="11">
                <v-text-field label="備註" v-model="list.note"></v-text-field>
              </v-col>
              <v-col cols="1">
                <v-btn
                  class="primary"
                  @click="addtoTempData"
                  v-if="process == 'add'"
                  >確認新增</v-btn
                >
              </v-col>
            </v-row>
          </v-form>

          <!-- 暫存區 -->
          <v-alert color="primary" outlined v-if="process == 'add'">
            <div class="d-flex">
              <h3>項目</h3>
              <v-spacer></v-spacer>
              <v-btn class="error" v-if="tempData.length > 0" @click="finishAdd"
                >完成新增作業</v-btn
              >
            </div>
            <v-simple-table fixed-header class="mt-2 text-no-wrap">
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th></th>
                    <th>建檔日期</th>
                    <th>收費項目</th>
                    <th>數量</th>
                    <th>備註</th>
                  </tr>
                </thead>
                <tbody class="tablebody">
                  <tr v-for="(item, index) in tempData" :key="index">
                    <td>
                      <v-icon color="red" @click.stop="delTempData(index)"
                        >mdi-close</v-icon
                      >
                      {{ index + 1 }}.
                    </td>
                    <td>{{ item.date }}</td>
                    <td>{{ item.fee.text }}</td>
                    <td>{{ item.quantity }}</td>
                    <td>{{ item.note }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-alert>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="success" @click="editOK" v-if="process == 'edit'"
            >確認修改</v-btn
          >
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //目前操作的表單內容
      oldList: {}, //存放修改前資料
      emptyRules: [(v) => !!v || "不可空白"],
      title: "", //抬頭
      titleClass: "", //抬頭顯示樣式
      process: "", //儲存執行動作
      feeItems: [], //所有費用
      tempData: [], //暫存目前想要新增的項目
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function (item) {
      this.dialog = true;
      this.enterEdit(item);
    });

    this.getFee();
  },

  methods: {
    enterAdd() {
      //執行新增流程
      this.process = "add";
      this.title = "新增計價單項目[日期鎖定為當日,非當日請註明於備註]";
      this.titleClass = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
        quantity: 1,
      };
      //2021/4/18:判斷有沒有超過26日 -> 超過就把日期調整成次月的1日
      let today = dayjs().format("YYYY-MM-DD");
      if (dayjs(today).format('DD') >= 26){
        this.list.date = dayjs(today).add(1,'month').format('YYYY-MM-01');
        this.list.note = today + '調整為' + this.list.date
      }
      //2021/4/18:判斷有沒有超過26日 -> 超過就把日期調整成次月的1日 end
    },
    enterEdit(item) {
      //執行修改流程
      this.process = "edit";
      this.title = "修改計價單項目";
      this.titleClass = "font-weight-black success lighten-2";
      this.oldList = item; //暫存未修改前資料
      this.list = { ...item }; //列表中複製一份顯示及修改用資料
    },
    addtoTempData() {
      if (this.$refs.form.validate()) {
        if (this.list.fee.snkey != undefined) {
          this.tempData.push({ ...this.list });
        } else {
          var pop = {
            msg: "資料輸入不正確!!請重新確認!!",
            type: true,
            theme: "warning",
          };
          this.$store.commit("snackbar", pop);
        }
      }
    },
    delTempData(index) {
      this.tempData.splice(index, 1);
    },
    //完成新增
    finishAdd() {
      this.$confirm("確認完成作業?").then((res) => {
        if (res) {
          this.$store.commit("getTime"); //取得現在時間
          var postData = this.tempData.map((i) => {
            return {
              user_snkey: this.$store.state.uData.snkey,
              fee_snkey: i.fee.snkey,
              date: i.date,
              start_day: i.date,
              end_day: i.date,
              quantity: i.quantity,
              price: i.fee.price,
              note: i.note,
              create_man:
                this.$store.state.pData.username +
                "(" +
                this.$store.state.time +
                ")", //執行動作的人,時間,內容
            };
          });

          /* 20201118判斷日期是否超過26日;20210418調整成開畫面取得今日日期時就判斷 */ 
          // postData = postData.filter((i) => {
          //   if (dayjs(i.date).format('DD') >= 26){
          //     i.date = dayjs(i.date).add(1,'month').format('YYYY-MM-01');
          //   }
          //   return i
          // });
          /* 20201106判斷日期是否超過26日 end*/


          this.axios
            .post(
              "general/addMulti/" +
                this.$store.state.databaseName +
                "/accounting",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已新增", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.list = {};
                this.tempData = [];
                this.dialog = false;
                this.$emit("getAllData");
              }
            });
        }
      });
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          snkey: this.list.snkey,
          user_snkey: this.$store.state.uData.snkey,
          fee_snkey: this.list.fee.snkey,
          date: this.list.date,
          start_day: this.list.date,
          end_day: this.list.date,
          quantity: this.list.quantity,
          price: this.list.fee.price,
          note: this.list.note,
          edit_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")", //執行動作的人,時間,內容
        };

        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/accounting",
            qs.stringify(postData)
          )
          .then((rs) => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                if (this.oldList[i] == undefined) {
                  this.$set(this.oldList, i, "");
                }
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    //取得所有會計帳目資料
    getFee() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/fee")
        .then((rs) => {
          var matchArr = "固定費類,車資類"; //這裡設定的是不顯示的類別
          var matchItems = rs.data.filter((i) => {
            return !matchArr.includes(i.kind) && !i.name.includes("(月)");
          });

          this.feeItems = matchItems.map((i) => {
            return {
              snkey: i.snkey,
              text: `${i.name} : ${i.kind}`,
              price: i.price,
            };
          });

          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
  },
};
</script>

<style lang="scss" scoped>
thead {
  th {
    font-size: 1.2rem;
    font-family: "微軟正黑體";
    color: red;
  }
}
tbody {
  td {
    font-size: 1rem;
  }
}
</style>