<template>
  <div class="pricelist">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">mdi-cash-usd-outline</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>計價單</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
              ></v-text-field>
              <popupadd @getAllData="getAllData" ref="enterEdit"></popupadd>
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>

      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="searchfilter"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead class="title">
                      <tr>
                        <th></th>
                        <th>建檔日期</th>
                        <th>收費項目</th>
                        <th>數量</th>
                        <th>備註</th>
                        <th>紀錄人</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="enterEdit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="enterDel(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.name}}</td>
                        <td>{{item.quantity}}</td>
                        <td>{{item.note}}</td>
                        <td>{{item.create_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>    {{items}}    </pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/document/PriceAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //活動參與資料
      serachKey: "", //搜尋欄
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      uData: "",
    };
  },

  computed: {
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  mounted() {
    this.auth = this.$store.state.pData; //各畫面權限暫存
    this.getAllData();
  },

  methods: {
    //取得病歷的會計帳
    getAllData() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "accounting/getAccountingByUserSnkey/" +
            this.$store.state.databaseName +
            "/" +
            this.$store.state.uData.snkey
        )
        .then((rs) => {
          var matchArr = "固定費類,車資類"; //這裡設定的是不顯示的類別
          var matchItems = rs.data.filter((i) => {
            return !matchArr.includes(i.kind);
          });

          this.items = matchItems.map((i) => {
            i.fee = {
              snkey: i.fee_snkey,
              text: i.name,
              price: i.price,
            };
            return i;
          });
          this.sortBy();
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    sortBy() {
      //依排序
      this.items = this.items.sort(function (a, b) {
        return a.date < b.date ? 1 : -1;
      });
      // this.sort = !this.sort;
    },
    enterEdit(item) {
      //進入修改作業 -> 顯示修改頁面
      this.$refs.enterEdit.$emit("childFn", item);
    },
    enterDel(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then((res) => {
        if (res) {
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "accounting",
            info: JSON.stringify(delData),
          };

          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/accounting",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
  },
};
</script>


<style lang="scss">
.pricelist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>